import picture from '../Images/profilePicture.jpg';
import React, { useState } from 'react';

function Card(){   

    const [skills, setSkills] = useState("My Name is Richard")
    const [count, setCount] = useState(0)

    return (
        <div className = 'container'>  
            <div className = 'card'>
                <img src= {picture} className='profile-pic' alt='Richard Lee'></img>               
                <h2>Hi!</h2>
                <h3>{skills}</h3>
                <div className='btn-group'>
                    <button onClick={() => setSkills("My Name is Richard")}>Name</button><span className = 'space'></span><button className= 'skill-btn' onClick ={() => setSkills("I am a Front-End Developer")}>Skills</button>
                </div>
                <div className='likes'>
                    <span className='counter'>{count}</span>
                    <span className='heart-icon' onClick={()=>setCount(count + 1)}><i className="fas fa-heart"></i></span>
                </div>
                <div className='social-icons'>
                    <span> <a href='https://www.linkedin.com/in/richard-lee-7b6b968b/' target= '_blank'><i class="fab fa-linkedin-in"></i></a></span>
                    
                    <span className='facebook'><a href='https://www.facebook.com/richard.t.lee1' target='_blank'><i class="fab fa-facebook-f"></i></a></span>                   
                </div>
            </div>          
            
        </div>
    )
}

export default Card;